import firebase from '@firebase/app'

export default {
  state: {
    posts: [],
    searchString: '',
  },
  mutations: {
    syncPost(state, post) {
      if (post != state.posts) {
        state.posts = post
      }
    },
    toSearch(state, value) {
      if (value != state.searchString) {
        state.searchString = value
      }
    },
  },
  actions: {
    async fetchPosts({ commit }) {
      try {
        const posts =
          (
            await firebase
              .database()
              .ref('/posts/')
              .once('value')
          ).val() || []
        const fbPosts = Object.keys(posts).map(key => ({
          ...posts[key],
          id: key,
        }))
        commit('syncPost', fbPosts)
        return fbPosts
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async createPost({ commit }, postData) {
      try {
        const post = await firebase
          .database()
          .ref(`/posts/`)
          .push(postData)
        return { ...postData, id: post.key }
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async updatePost({ commit }, postData) {
      try {
        await firebase
          .database()
          .ref(`/posts/`)
          .child(postData.id)
          .update(postData)
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async deletePost({ commit }, postId) {
      try {
        await firebase
          .database()
          .ref(`/posts/`)
          .child(postId)
          .remove()
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
  },
  getters: {
    posts: s => s.posts,
    postById: s => id => s.posts.find(p => p.id === id),
    searchString: s => s.searchString,
    hots: s => s.posts.filter(p => p.hot),
  },
}
