import Vue from 'vue'
import Vuex from 'vuex'
import Post from './modules/post'
import Auth from './modules/auth'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    error: null,
    sidebarActive: false,
  },
  mutations: {
    setError(state, error) {
      state.error = error
    },
    clearError(state) {
      state.error = null
    },
    toggleSidebar(state) {
      state.sidebarActive = !state.sidebarActive
    },
  },
  actions: {},
  getters: {
    errors: s => s.error,
    sidebarActive: s => s.sidebarActive,
  },
  modules: { Post, Auth },
})
