<template>
  <nav class="navbar navbar-expand-lg navbar-dark nav-theme">
    <div class="container">
      <router-link class="navbar-brand mr-0 py-0" to="/">
        <span class="logo">VUE REALTY</span>
      </router-link>
      <button
        class="btn navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div id="navbarSupportedContent" class="collapse navbar-collapse">
        <form
          v-if="this.$route.name === 'post' || this.$route.name === 'catalog'"
          class="form-inline ml-lg-4 mt-2 mb-3 my-lg-0 mr-auto"
        >
          <input
            id="search-input"
            v-model.trim="searchString"
            class="post_form-control"
            title="Поиск по всем объектам"
            autocomplete="off"
            @input="toSearch(searchString)"
          />
          <i class="icon search"></i>
        </form>
        <ul class="navbar-nav ml-lg-auto">
          <router-link
            v-if="this.$route.name !== 'create-post' && isAdmin"
            tag="li"
            class="nav-item btn-add mr-lg-2"
            to="/create-post"
          >
            <a class="nav-link">
              <i class="icon add"></i>
              <span>Добавить участок</span>
            </a>
          </router-link>
          <router-link
            v-if="this.$route.name !== 'post' && this.$route.name !== 'catalog'"
            tag="li"
            class="nav-item mx-lg-2"
            to="/catalog"
          >
            <a class="nav-link" title="Имущество компании"
              >Имущество компании</a
            >
          </router-link>
          <router-link
            tag="li"
            class="nav-item"
            :class="[isAdmin ? 'mx-lg-2' : 'ml-lg-2']"
            to="/contacts"
          >
            <a :class="[!isAdmin ? 'pr-0' : '']" class="nav-link">Контакты</a>
          </router-link>
          <li v-if="isAdmin" class="nav-item ml-lg-2">
            <a class="nav-link pr-0" href @click.prevent="logout">Выйти</a>
          </li>
        </ul>
      </div>
      <!--Supported content-->
    </div>
    <!--container-->
  </nav>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Navbar',
  // eslint-disable-next-line vue/require-prop-types
  data: () => ({
    searchString: '',
  }),
  computed: {
    ...mapGetters(['isAdmin']),
  },
  methods: {
    async logout() {
      try {
        await this.$store.dispatch('logout')
        this.$router.push('/login?message=logout')
        // eslint-disable-next-line no-empty
      } catch (e) {}
    },
    toSearch(value) {
      this.$store.commit('toSearch', value)
    },
  },
}
</script>

