import firebase from '@firebase/app'

export default {
  state: {
    admin: false,
  },
  mutations: {
    checkAdmin(state, value) {
      state.admin = value
    },
  },
  actions: {
    async login({ commit }, { email, password }) {
      try {
        await firebase.auth().signInWithEmailAndPassword(email, password)
        if (await firebase.auth().currentUser) {
          commit('checkAdmin', true)
        }
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async logout({ commit }) {
      try {
        await firebase.auth().signOut()
        commit('checkAdmin', false)
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async checkAdmin({ commit }) {
      try {
        ;(await await firebase.auth().currentUser)
          ? commit('checkAdmin', true)
          : commit('checkAdmin', false)
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
  },
  getters: {
    isAdmin: s => s.admin,
  },
}
